@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
}
.benefits-group {
    display: flex;
    justify-content: space-between;
}

.benefits-group img {
    margin-top: 10%;
}

h4 {  
    font-weight: 600;
    font-size: 48px;
    line-height: 72px;
    letter-spacing: -1px;
    margin-top: 20%;
}

.benefits-drop {
    display: flex;
    flex-direction: column;
    margin-left: 10%;
}

.benefits-drop p {
    font-weight: 500;
    font-size: 14px;
    line-height: 26px;
    display: flex;
    align-items: center;
    letter-spacing: 2px;
    padding: 3% 0%;
    border-bottom: 2px dotted #e9e9e9;
}

.active {
    font-size: 12px;
    line-height: 20px;
    opacity: 0.6;
}

@media screen and (max-width: 1080px) {
    .benefits-group {
        flex-direction: column;
    }

    .benefits-group img {
        width: 80%;
        align-self: flex-end;
    }

    .benefits-drop {
        width: 80%;
        margin-left: 10%;
    }

    h4 {
        font-weight: normal;
        font-size: 28px;
    }
}
.contact {
    display: flex;
    flex-direction: column;
    margin-top: 5%;
}

.contact-top {
    display: flex;
    justify-content: space-evenly;
}

.contact-top-left,
.contact-top-mid,
.contact-top-right {
    display: flex;
    flex-direction: column;
}

.contact-top-left .head {
    font-weight: 600;
    font-size: 25.0119px;
    line-height: 150%;
    color: #404595;
}

.contact-top-mid .head,
.contact-top-right .head {
    font-weight: 500;
    font-size: 17px;
    line-height: 25px;
    letter-spacing: -0.231818px;
    color: #3F4494;
}

.items {
    font-size: 15px;
    line-height: 22px;
    letter-spacing: -0.204545px;
    color: #1E2833;
    opacity: 0.6;
    text-align: left;

    margin-top: 15%;
}

.contact-bottom {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.contact-bottom-icons {
    display: flex;
    justify-content: space-around;
    width: 20%;
    margin-top: 10%;
}

@media screen and (max-width: 1080px) {
    
    .contact-top {
        flex-direction: column;
        margin-left: 10%;
    }

    .items {
        margin-top: 5%;
    }

    .contact-top-mid .head, 
    .contact-top-right .head {
        margin-top: 5%;
    }
}
.hero-container {
    background: url(/reactJS-health.io-SPA/static/media/bg.7917462b.svg);
    background-size: contain;
    background-repeat: no-repeat;
    padding-bottom: 20px;
}

.navbar-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding-top: 2%;
    align-items: center;
    text-align: center;
    color: #FFFFFF;
}

.nav-items {
    display: flex;
    align-items: center;
    text-align: center;
}

.logo {
    font-weight: 600;
    margin-right: 30%;
}

a {
    text-decoration: none;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
    margin: 0px 20px;
}

.nav-items > button {
    background: #FFFFFF;
    -webkit-backdrop-filter: blur(66px);
            backdrop-filter: blur(66px);
    border-radius: 40px;
    border: 1px solid #FFFFFF;

    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #3A3790;
    padding: 10px 35px;
}

.hero-container-main {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 5% 0% 5% 0%;
}

.hero-group {
    display: flex;
    flex-direction: column;
    width: 35%;
}

.hero-group .head {
    font-weight: 600;
    font-size: 54px;
    line-height: 150%;
    color: #FFFFFF;
}

.hero-group .desc {
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.4px;
    color: #FFFFFF;
}

.hero-container-main img {
    margin-top: 30px;
}

@media screen and (max-width: 1080px) {
    .navbar-container,
    .nav-items {
        flex-direction: column;
    }
    
    .nav-items > a{
        margin-bottom: 10%;
    }

    .logo {
        margin: 5% 70% 10% 0%;
    }

    .hero-container-main {
        flex-direction: column;
        margin-left: -20%;
        margin-top: 20%;
    }

    .hero-container-main img {
        margin-left: 20%;
        width: 70%;
    }

    .hero-container {
        background-size: auto;
    }

    .hero-group .head {
        font-size: 22px;
        line-height: 28px;
    }
}
.plans {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    align-items: center;
    text-align: center;

    background: url(/reactJS-health.io-SPA/static/media/bg.7917462b.svg);
    background-size: contain;
    background-repeat: no-repeat;
    margin-top: 50px;
}

.plans-head {
    font-weight: 600;
    font-size: 48px;
    line-height: 72px;
    letter-spacing: -1px;
    color: #FFFFFF;
    margin-top: 5%; 
}

.plans-desc {
    font-size: 20px;
    line-height: 30px;
    width: 35%;
    margin: 10px 0px 50px 0px;
    color: #FFFFFF;
}

.plans-card {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;

    background: #F8F8F8;
    border-radius: 5px;
    box-shadow: 0px 4px 15px rgba(32, 35, 21, 0.1);

    width: 300px;
    padding: 30px 0px 30px 0px;
    margin: 0% 0.5% 10% 0.5%;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}   

.container p {
    width: 100%;
    margin-bottom: 5%;
    font-size: 16px;
    line-height: 22px;
    color: #746E7B;
}

.container img {
    opacity: 0.6;
}

.container button {
    padding: 0.8rem 4rem;
    margin-top: 70px;
    background: #cdcdf7;
    opacity: 0.87;
    border-radius: 100px;
    border: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: #383A96;
 }

article {
    margin: 10% 0% 5% 0%;
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    color: #232427;
}

sub {
    font-size: 16px;
    line-height: 20px;
    color: #746E7B;
    opacity: 0.5;
}

.plan-price {
    width: 100%;
    padding: 7%;
    margin-bottom: 20%;
    background: #FFFFFF;
}

.plans-card > :hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background: linear-gradient(151deg, #3C448E 0%, #696DA8 100%);
    color: #FFFFFF;
}

.container:hover > article,
.container:hover > div > p,
.container:hover > p,
.container:hover > article > sub {
    color: #FFFFFF;
}

.container:hover > button {
    background: #FFFFFF;

}

.container:hover > .plan-price {
    background: linear-gradient(to right, #3c448e, #696da8);
    color: #FFFFFF;
}

.plans-group {
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: center;
}

.plans-mid-head {
    font-weight: 600;
    font-size: 48px;
    line-height: 72px;
    text-align: center;
    letter-spacing: -1px;
    width: 50%;
    margin: 10% 0% 5% 0%;
}

td, th, tr {
    border-bottom: 2px dotted #e9e9e9;
    padding: 2% 0% 2% 0%;
}

table {
    width: 80%;
}

.plans > img {
    margin: 10% 0% 10% 0%;
}

.to-left {
    text-align: left;
}

@media screen and (max-width: 1080px) {
    #faqs {
        width: 95%;
    }

    table {
        width: 90%;
        font-size: 12px;
    }

    th {
        padding-right: 10px;
    }

    .plans-mid-head {
        font-size: 22px;
        line-height: 28px;
    } 

    .plans-card {
        flex-direction: column;
    }

    .plans {
        background-size: auto;
    }
}
.waitlist {
    display: flex;
    background: linear-gradient(131deg, #1d288b 0%, #6d70ad 100%);
    color:  #FFFFFF;
}

.waitlist-group {
    display: flex;
    flex-direction: column;
    width: 40%;
    margin-top: 10%;
}

.waitlist-group .head {
    font-weight: 600;
    font-size: 48px;
    line-height: 72px;
    letter-spacing: -1px;
}

.waitlist-group .desc {
    font-size: 18px;
    line-height: 27px;
    margin: 2% 0% 5% 0%;
}

.caps {
    font-weight: 500;
    font-size: 14px;
    line-height: 26px;
    letter-spacing: 2px;
    margin-top: 5%;
}

.waitlist-group input {
    padding: 15px 30px;
    width: 80%;
    background: transparent;
    color: white;
    border: 1.5px solid #EBEAED;
    box-sizing: border-box;
    border-radius: 4px;
}

.waitlist-group button {
    background: #FFFFFF;
    border-radius: 100px;
    border: none;
    padding: 10px 35px;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    color: #363C90;
}

.waitlist-icons {
    display: flex;
    align-items: center;
    justify-content: center;
    justify-content: flex-start;
    margin: 10% 0% 10% 0%;
}

.waitlist-icons p,
.waitlist-icons img {
    margin-left: 20px;
}


.waitlist-icons p {
    font-size: 14px;
    line-height: 26px;
    letter-spacing: 2px;
}

.referral-text {
    font-size: 12px;
    line-height: 24px;
    width: 80%;
    margin-bottom: 20%;
}

@media screen and (max-width: 1080px) {
    .waitlist {
        flex-direction: column;
    }

    .waitlist-group {
        width: 90%;
        margin-left: 10%;
    }
}
